import React, { Component } from "react";
import Alert from "@material-ui/lab/Alert";
import * as Yup from "yup";
import axios from "../../axios";
import { Formik, Form, Field } from "formik";
import { Button, Container, Typography, Grid } from "@material-ui/core";
import { TextField } from "formik-material-ui";
import { connect } from "react-redux";
import PuffLoader from "react-spinners/PuffLoader";
import { MDBTypography } from "mdbreact";
import Feeassign from "./Feeassign";
const mapStateToProps = (state) => ({
  collegeID: state.collegeID,
});
class Feeupdate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      formdata: '',
      loadding: false,
      invalidregno: false,
      showData: false,
    };
  }
  render() {
    return (
      <React.Fragment>
        <Container maxWidth="md">
          <Typography component="div" style={{ height: "5vh" }} />
          <MDBTypography
            tag="h2"
            variant="h2-responsive"
            className="text-center"
            colorText="teal"
          >
            Student Fee Update
          </MDBTypography>
          <div className="text-center">
            <Formik
              initialValues={{ RegistrationNo: "" }}
              validationSchema={Yup.object({
                FormNo: Yup.string()
                  .max(15, "Invalid Form Number")
                  .min(1, "Invalid Form Number")
                  .required("Enter Student Form Number"),
              })}
              onSubmit={(values, { setSubmitting }) => {
                this.setState({
                  loadding: true,
                  invalidregno: false,
                  showData: false,
                });
                setTimeout(() => {
                  setSubmitting(true);
                  axios
                    .get(
                      "api/admission/search/fee/" +
                        values.FormNo +
                        "/" +
                        this.props.collegeID,
                      {}
                    )
                    .then((res) => {
                      if (res.data.flag == "0") {
                        setSubmitting(false);
                        this.setState({
                          loadding: false,
                          invalidregno: true,
                          showData: false,
                        });
                      } else if (res.data.flag == "1") {
                        setSubmitting(false);
                        this.setState({
                          formdata: res.data.data[0],
                          loadding: false,
                          showData: true,
                        });
                      }
                    })
                    .catch(function (error) {
                      alert("Error occured:" + error);
                      setSubmitting(false);
                    });
                }, 500);
              }}
            >
              {({ submitForm, isSubmitting, values }) => (
                <Form>
                  <Grid container spacing={3}>
                    <Grid item lg={3} xs={1}>
                      {" "}
                    </Grid>

                    <Grid item lg={6} xs={10}>
                      {this.state.invalidregno ? (
                        <Alert severity="error">
                          {" "}
                          Invalid Form No. Enter a Valid Form No{" "}
                        </Alert>
                      ) : null}
                    </Grid>
                    <Grid item lg={3} xs={1} />

                    <Grid item lg={3} xs={1}>
                      {" "}
                    </Grid>

                    <Grid item lg={6} xs={10}>
                      <Field
                        component={TextField}
                        name="FormNo"
                        type="text"
                        label="Form No"
                        variant="outlined"
                        fullWidth
                      />
                    </Grid>
                    <Grid item lg={3} xs={1}></Grid>

                    <Grid item lg={12} xs={12}>
                      <div
                        style={{
                          display: "flex",
                          justifyContent: "center",
                          alignItems: "center",
                          marginTop: "3rem",
                        }}
                      >
                        <Button
                          variant="contained"
                          color="primary"
                          size="large"
                          disabled={isSubmitting}
                          onClick={submitForm}
                        >
                          Get Form
                        </Button>
                      </div>
                    </Grid>

                    <Grid item lg={12} xs={12}></Grid>
                    <Grid item lg={4} xs={12}>
                      {" "}
                    </Grid>
                  </Grid>
                </Form>
              )}
            </Formik>
          </div>
        </Container>

        <div style={{ height: "100%", width: "100%" }}>
          {this.state.showData ? (
            <Feeassign data={this.state.formdata}/>
          ) : (
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <PuffLoader
                size={80}
                color={"#283593"}
                loading={this.state.loadding}
              />
            </div>
          )}

          <Typography component="div" style={{ height: "30vh" }} />
        </div>
      </React.Fragment>
    );
  }
}

export default connect(mapStateToProps)(Feeupdate);
