import React, { useState, useEffect } from "react";
import axios from "../../axios";
import { Formik, Form, Field } from "formik";
import { TextField, Select } from "formik-material-ui";
import {
  Button,
  Grid,
  Container,
  FormControl,
  InputLabel,
  MenuItem,
  Paper,
} from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  textbox: {
    backgroundColor: "#fff",
    transform: "upper",
  },
}));

export default function Feeassign({ data }) {
  const classes = useStyles();
  const [formData, setFormData] = React.useState({
    ID: "",
    Course_name: "",
    firstName: "",
    middleName: "",
    lastName: "",
    Subject1: "",
    Subject2: "",
    Subject3: "",
    Subject4: "",
    Subject5: "",
    Subject6: "",
    fee_id: "",
    Fee: "",
  });
  useEffect(() => {
    setFormData(data);
  }, []);

  return (
    <>
      <Container maxWidth="lg">
        <Paper elevation="3">
          <div
            style={{
              border: "1px",
              borderStyle: "Solid",
              borderColor: "#e2e2e2",
              backgroundColor: "#f2fcff",
              padding: "30px",
            }}
          >
            <Formik
              initialValues={formData}
              enableReinitialize
              onSubmit={(values) => {
                //setFormData(values);
                //alert(JSON.stringify(values))
                setTimeout(() => {
                  axios
                    .post(
                      "api/admissions/fee/update",
                      JSON.stringify(values),
                      {
                        headers: {
                          // Overwrite Axios's automatically set Content-Type
                          "Content-Type": "application/json",
                          Accept: "application/json",
                        },
                      }
                    )
                    .then((res) => {
                      if (res.status === 200) {
                        alert("FOrm Updated Successfully");
                      }
                    })
                    .catch(function (error) {
                      alert("Error occured:" + error);
                    });
                }, 500);
              }}
            >
              {({ values }) => (
                <Form>
                  <Grid container direction={"row"} spacing={3}>
                    <Grid item lg={3} xs={12}>
                      <div style={{ textAlign: "left" }}>
                        <label className="formreviewLabel">
                          <strong>College Form No</strong>
                          <div
                            className="formreview"
                            style={{ color: "#888b8c" }}
                          >
                            {values.ID}
                          </div>
                        </label>
                      </div>
                    </Grid>
                    <Grid item lg={3} xs={12}>
                      <div style={{ textAlign: "left" }}>
                        <label className="formreviewLabel">
                          <strong>Student Name</strong>
                          <div
                            className="formreview"
                            style={{ color: "#888b8c" }}
                          >
                            {values.firstName +
                              " " +
                              values.middleName +
                              " " +
                              values.lastName}
                          </div>
                        </label>
                      </div>
                    </Grid>
                    <Grid item lg={3} xs={12}>
                      <div style={{ textAlign: "left" }}>
                        <label className="formreviewLabel">
                          <strong>Course Name</strong>
                          <div
                            className="formreview"
                            style={{ color: "#888b8c" }}
                          >
                            {values.Course_name}
                          </div>
                        </label>
                      </div>
                    </Grid>
                    <Grid item lg={3} xs={12}></Grid>
                    <Grid item lg={3} xs={12}>
                      <div style={{ textAlign: "left" }}>
                        <label className="formreviewLabel">
                          <strong>Subject 1</strong>
                          <div
                            className="formreview"
                            style={{ color: "#888b8c" }}
                          >
                            {values.Subject1}
                          </div>
                        </label>
                      </div>
                    </Grid>
                    <Grid item lg={3} xs={12}>
                      <div style={{ textAlign: "left" }}>
                        <label className="formreviewLabel">
                          <strong>Subjet 2</strong>
                          <div
                            className="formreview"
                            style={{ color: "#888b8c" }}
                          >
                            {values.Subject2}
                          </div>
                        </label>
                      </div>
                    </Grid>
                    <Grid item lg={3} xs={12}>
                      <div style={{ textAlign: "left" }}>
                        <label className="formreviewLabel">
                          <strong>Subject 3</strong>
                          <div
                            className="formreview"
                            style={{ color: "#888b8c" }}
                          >
                            {values.Subject3}
                          </div>
                        </label>
                      </div>
                    </Grid>
                    <Grid item lg={3} xs={12}>
                      <div style={{ textAlign: "left" }}>
                        <Field
                          component={TextField}
                          className={classes.textbox}
                          name="Fee"
                          type="text"
                          label="Fee"
                          variant="outlined"
                          fullWidth
                          margin="dense"
                        />
                      </div>
                    </Grid>
               

                    <Grid item lg={9} xs={12}>
                      <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        fullWidth
                        style={{ height: "2.5rem" }}
                      >
                        Update
                      </Button>
                    </Grid>
                    <Grid item lg={3} xs={12}></Grid>
                  </Grid>
                </Form>
              )}
            </Formik>
          </div>
        </Paper>
      </Container>
    </>
  );
}
